body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.hideMe {
    display: none;
}

.homepageToggleBar {
  width: 60%;
  margin: auto;
}

.pageToggleButton {
  width: 400px;
  padding: 1vh;
  margin-bottom: 15px;
}

.signupSpacer {
    width: 80%;
    padding: 5vh;
    background-color: aliceblue;
    text-align: justify;
    margin-left: auto;
    margin-right: auto;
    margin-top: 4vh;
}

button.submitButton {
  margin: 2vh;
}

.signupBody {
    background-color: lightblue;
    min-height: 20vh;
    font-size: calc(5px + 2vmin);
    color: black;
    padding: 15vh;
    margin: 5vh;
    line-height: 5vh;
  }

P.alreadyMember {
 display: inline-block;
 margin: auto;
 padding-left: 20vh;
 font-size: 25px;
  }

input.form-control {
      margin-left: 15px;
  }
.pageSwitchBar {
    text-align: center;
    max-height: 5vh;
    margin: 0px;
}

A.pageToggle {
    color: white;
}

.search-post-form {
    text-align: center;
    margin: 10px;
    letter-spacing: 4px;
}
  ul.App-navbar {
    border-color: rgb(255, 169, 169);
    border-width: 2px;
    border-style: inset;
    position: -webkit-sticky;
    position: sticky;
    padding: 10px;
    background-color: #282c34;
    height: 10vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    font-size: calc(12px + 2vmin);
    color: white;
    text-transform: uppercase;
  }

  li.menuLink {
    list-style-type: none;
    margin-left: 5px;
    margin-right: 5px;
    padding: 20px;
    font-size: calc(8px + 2vmin);
  }

  img.logo {
    width: 50px;
  }
  
  A:hover.logo { text-decoration: none; }

  A.menuLink { color: white }

  A:link.menuLink { text-decoration: none; padding: 15px;}

  A:active.menuLink { text-decoration: none; }

  A:hover.menuLink { color: black; text-decoration: none; background-color: rgb(197, 197, 197); border-radius: calc(8px + 2vmin); border-color: black; border-width: 1px; }

.landingBar {
    text-align: center;
    margin: auto;
    padding: 25px;
}

.landingTitle {
    font-size: 4vh;
    margin-left: 25px;
    margin-right: 25px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s ease-in-out;
          animation: App-logo-spin infinite 10s ease-in-out;
  height: 60px;
}

.App-header {
  background-color: #222;
  min-height: 3vh;
  padding: 1vh;
  color: white;
  margin: 0px;
}

.App-body {
  min-height: 30vh;
  margin: auto;
  padding-top: 5vh;
  text-align: center;
}

.App-footer {
  text-align: center;
  font-size: 25px;
  min-height: 10vh;
  margin-top: 45px;
  position: -webkit-sticky;
  position: sticky;
  border: 2px;
  border-color: black;
  background-color: green;
  line-height: 80px;
  }

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

